.container {
    display: grid;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
}

.write-task {
    height: 30px;
    width: 250px;
    padding-left: 10px;
    background-color: #ffffff2c;
    color: #fff;
    border-radius: 5px;
    word-wrap: break-word;
}

.write-task::placeholder {
    color: #fff;
}

button {
    height: 29px;
    width: 90px;
    border-radius: 5px;
    background-color: rgb(26, 56, 149);
    color: #fff;
    margin-left: 10px;
}

span {
    color: #fff;
}

.task-item {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    height: 50px;
    width: 250px;
    background-color: #ffffff2c;
    padding: 5px;
    border: 1px solid #878484;
    border-radius: 5px;
    margin-top: 10px;
}

.task-icons {
    width: 60px;
    display: flex;
    justify-content: space-between;
    margin-top: 7px;
}

.edit-icon {
    color: rgb(57, 121, 27);
}

.delete-icon {
    color: rgb(239, 49, 19);
}

.check-icon {
    color: rgb(21, 67, 166);
}

.task-text {
    font-size: 20px;
}

.date-time {
    font-size: 12px;
    margin-top: 15px;
    position: absolute;
    margin-top: 25px;
}

.edit-task {
    display: flex;
    position: fixed;
    background-color: #fff;
    border-radius: 5px;
}

.edit-task input {
    height: 30px;
    width: 200px;
    background-color: #ffffffdd;
    border-radius: 5px;
}

.update {
    margin-top: 0.2px;
}